@font-face {
  font-family: "Roboto";
  src: url("../fonts/Roboto-Regular.woff2") format("woff2");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "Roboto";
  src: url("../fonts/Roboto-Bold.woff2") format("woff2");
  font-weight: 700;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "Roboto";
  src: url("../fonts/Roboto-Black.woff2") format("woff2");
  font-weight: 900;
  font-display: swap;
  font-style: normal;
}
html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  color: #fff;
  font-family: "Roboto", sans-serif;
  font-size: 20px;
}

img {
  max-width: 100%;
}

a {
  text-decoration: none;
  color: inherit;
}

.visually-hidden {
  position: absolute !important;
  overflow: hidden;
  clip: rect(1px 1px 1px 1px);
  clip: rect(1px, 1px, 1px, 1px);
  width: 1px;
  height: 1px;
  padding: 0;
  border: 0;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.site-container {
  overflow: hidden;
}

.btn {
  padding: 0;
  position: relative;
  border: none;
  cursor: pointer;
  color: #fff;
  text-transform: uppercase;
}

.btn::after,
.link::after {
  content: " ";
  position: absolute;
  height: 100%;
  width: 0;
  top: 0;
  left: 0;
  background-color: rgba(255, 255, 255, 0.4);
  transition: all 0.7s ease;
  mix-blend-mode: overlay;
}

.btn:hover::after,
.link:hover::after {
  width: 100%;
}

.btn:disabled {
  opacity: 0.5;
  cursor: default;
}

.btn:disabled:hover::after {
  width: 0;
}

.link {
  position: relative;
  z-index: 20;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  color: #fff;
  text-transform: uppercase;
}

.btns-menu {
  position: relative;
  width: 100%;
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding: 10px 15px;
  margin-bottom: 45px;
  background-color: rgba(255, 255, 255, 0.4);
}
.btns-menu__box {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-evenly;
}
.btns-menu__list {
  display: flex;
  align-items: center;
  justify-content: center;
  flex: 0 0 auto;
  flex-wrap: wrap;
}
.btns-menu__btn {
  margin: 5px 7px;
  padding: 5px 15px;
  font-size: 18px;
  font-weight: 700;
}
.btns-menu__btn:not(:last-child) {
  margin-right: 10px;
}
.btns-menu__home {
  height: 30px;
  width: 40px;
  margin-right: 10px;
  font-size: 18px;
  font-weight: 700;
  background-color: #333;
}
.btns-menu__icon {
  display: none;
}
.btns-menu__btn-ruls {
  background-color: #333;
}

.main {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #782a78;
  background: linear-gradient(285deg, #813281 0%, #f59056 63%, #e9af43 98%);
}
.main__item {
  width: 100%;
}
.main__item:not(:last-child) {
  margin-bottom: 20px;
}
.main__link {
  width: 100%;
  padding: 15px 25px;
  text-align: center;
  font-size: 25px;
  font-weight: 900;
  background-color: rgba(255, 255, 255, 0.4);
  color: #333;
}

.tic-tac-toe {
  height: 100vh;
  background: #56b4d3;
  background: linear-gradient(65deg, #56b4d3 7%, #9e6feb 35%, #c15746 90%);
  height: 100vh;
  display: flex;
  align-items: center;
  flex-direction: column;
}
.tic-tac-toe__btn {
  background-color: #1c6d87;
}
.tic-tac-toe__restart {
  background-color: #c15746;
}
.tic-tac-toe__game {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 15px;
  margin-bottom: 25px;
}
.tic-tac-toe__cell {
  width: 80px;
  height: 80px;
  background-color: rgba(255, 255, 255, 0.4);
}
.tic-tac-toe__win-text {
  margin: 0;
  font-family: "Roboto", serif;
  font-size: 40px;
  text-align: center;
  color: #fff;
  font-weight: 900;
  text-shadow: 3px 3px 7px rgba(0, 0, 0, 0.5);
}

.pointer {
  cursor: pointer;
}

.circle {
  width: 100%;
  height: 100%;
  stroke-dasharray: 283;
  stroke-dashoffset: 283;
  animation: draw 1s 1.5s forwards;
}

.cross {
  width: 100%;
  height: 100%;
}

.cross-first {
  stroke-dasharray: 125;
  stroke-dashoffset: 125;
  animation: draw 0.6s forwards;
}

.cross-second {
  stroke-dasharray: 125;
  stroke-dashoffset: 125;
  animation: draw 0.6s 0.6s forwards;
}

@keyframes draw {
  100% {
    stroke-dashoffset: 0;
  }
}
.snake {
  height: 100vh;
  position: relative;
  display: flex;
  align-items: center;
  flex-direction: column;
  background: #229a87;
  background: linear-gradient(180deg, #cff691 0%, #1f8b7b 80%, #349fdd 100%);
}
.snake__btn {
  background-color: #ce9360;
}
.snake__restart {
  background-color: #4782a6;
}
.snake__control-btn {
  width: 55px;
  height: 55px;
  font-size: 30px;
  border: none;
  cursor: pointer;
  color: #fff;
  background-color: #116190;
  text-align: center;
}
.snake__control-btn:not(:last-child) {
  margin-bottom: 5px;
}
.snake__score-text {
  margin: 25px 0 0;
  font-family: "Roboto", serif;
  font-size: 30px;
  text-align: center;
  color: #fff;
  font-weight: 900;
  text-shadow: 3px 3px 7px rgba(0, 0, 0, 0.5);
}

.snake-box.active {
  display: flex;
  flex-direction: column;
}

.snake-box {
  position: absolute;
  display: none;
}

.snake__box-left {
  bottom: 15px;
  left: calc(50% - 60px);
  transform: translateX(-50%);
}

.snake__box-center {
  bottom: 15px;
  left: 50%;
  transform: translateX(-50%);
}

.snake__box-right {
  bottom: 15px;
  left: calc(50% + 60px);
  transform: translateX(-50%);
}

.popup {
  position: fixed;
  z-index: 50;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  visibility: hidden;
  transition: all 0.7s ease;
}
.popup.active {
  opacity: 1;
  visibility: visible;
}
.popup__body {
  display: flex;
  width: 100%;
  height: 100%;
  justify-content: center;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.4);
}
.popup__content {
  position: relative;
  padding: 20px 25px 30px;
  overflow-y: auto;
  max-height: 60vh;
  background: #121212;
  background: linear-gradient(58deg, #121212 10%, #333333 98%);
}
.popup__title {
  text-transform: uppercase;
  text-align: center;
  font-size: 20px;
  margin-top: 0;
  margin-bottom: 25px;
}
.popup__text {
  max-width: 75vw;
}
.popup__text p {
  margin: 10px 0;
}
.popup__close-btn {
  position: absolute;
  height: 30px;
  width: 40px;
  top: 15px;
  right: 25px;
  display: block;
  padding: 9px 10px;
  background-color: transparent;
  color: #fff;
  transition: all 0.7s ease;
}
.popup__close-btn::before {
  content: "";
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  position: absolute;
  z-index: 3;
  border: 3px solid #fff;
}
.popup__close-btn span {
  position: absolute;
  width: 20px;
  height: 3px;
  display: block;
  background-color: #fff;
  transition: all 0.5s ease;
}
.popup__close-btn span:first-child {
  transform: rotate(-25deg) translateY(-50%);
  top: 50%;
}
.popup__close-btn span:last-child {
  transform: rotate(25deg) translateY(50%);
  bottom: 50%;
}
.popup__close-btn:hover {
  background-color: #fff;
}
.popup__close-btn:hover span {
  background-color: #333;
}

@media (max-width: 1100px) {
  .btns-menu__list {
    top: 0;
    left: 0;
    position: fixed;
    width: 100%;
    height: 100%;
    z-index: 10;
    display: block;
    text-align: center;
    overflow: auto;
    padding: 70px 20px;
    background: rgba(227, 236, 236, 0.75);
    transform: translateX(100%);
    transition: all 0.5s ease;
  }
  .btns-menu__list::before {
    content: "";
    width: 100%;
    height: 50px;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 2;
    background: rgba(227, 236, 236, 0.9);
    transition: all 0.5s ease;
    transform: translateX(100%);
    transition: all 0.5s ease;
  }

  .btns-menu__icon {
    position: relative;
    z-index: 15;
    height: 30px;
    width: 40px;
    display: block;
    background-color: #333;
    padding: 9px 10px;
    transition: all 0.5s ease;
  }
  .btns-menu__icon::before {
    content: "";
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    position: absolute;
    border: 3px solid transparent;
  }
  .btns-menu__icon span {
    width: 20px;
    height: 3px;
    display: block;
    background-color: #fff;
    transition: all 0.5s ease;
  }
  .btns-menu__icon span:first-child {
    position: absolute;
    top: 7px;
  }
  .btns-menu__icon span:last-child {
    position: absolute;
    bottom: 7px;
  }

  .btns-menu__list.active {
    transform: translateX(0);
  }
  .btns-menu__list.active::before {
    transform: translateX(0);
  }

  .btns-menu__icon.active {
    background-color: transparent;
  }
  .btns-menu__icon.active::before {
    animation: active-menu 0.4s 0.25s forwards;
  }
  .btns-menu__icon.active span {
    background-color: #333;
  }
  .btns-menu__icon.active span:first-child {
    transform: rotate(-25deg) translateY(-50%);
    top: 50%;
  }
  .btns-menu__icon.active span:last-child {
    transform: rotate(25deg) translateY(50%);
    bottom: 50%;
  }
  .btns-menu__icon.active span:nth-child(2) {
    transform: scale(0);
  }

  .btns-menu__btn {
    min-width: 80%;
    padding: 10px 15px;
    font-size: 20px;
  }

  .btns-menu__home {
    margin-right: 0;
  }

  .snake__game {
    order: 1;
  }

  .snake__score-text {
    margin: 0 0 25px;
  }

  .snake__control-btn:not(:last-child) {
    margin-bottom: 5px;
  }
}
@media (max-width: 450px) {
  .popup__title {
    text-align: left;
  }

  .popup__text {
    font-size: 18px;
  }

  .main__link {
    padding: 15px;
  }

  .snake__control-btn {
    width: 50px;
    height: 50px;
    font-size: 27px;
  }

  .snake__control-btn:not(:last-child) {
    margin-bottom: 10px;
  }
}
@keyframes active-menu {
  0% {
    border-color: transparent;
  }
  25% {
    border-color: transparent #333 transparent transparent;
  }
  50% {
    border-color: transparent #333 #333 transparent;
  }
  75% {
    border-color: transparent #333 #333 #333;
  }
  100% {
    border-color: #333;
  }
}